@layer base {
  html {
    scroll-padding-top: 100px;
  }
  html, body {
    font-size: 15px;
  }
  @screen md {
    html, body {
      font-size: 15px;
    }
  }
  @screen xl {
    html, body {
      font-size: 1vw;
    }
  }
  .table-responsive {
    @apply overflow-auto;
  }
  @media (max-width: 767.98px) {
    .table-responsive table {
      width: 900px !important;
    }
  }
  .img-full img {
    @apply w-full h-full object-cover;
  }
  .ratio-contain {
    position: relative;
    display: block;
    height: 0;
    overflow: hidden;
    padding-top: 100%;
  }
  .ratio-contain img, .ratio-contain iframe {
    @apply absolute top-0 left-0 w-full h-full object-contain transition-all duration-300;
  }
  .img-ratio {
    position: relative;
    display: block;
    height: 0;
    overflow: hidden;
    padding-top: 100%;
  }
  .img-ratio img, .img-ratio iframe {
    @apply absolute top-0 left-0 w-full h-full object-cover transition-all duration-300;
  }
  .img-ratio-center {
    position: relative;
    height: 0;
    overflow: hidden;
    padding-top: 100%;
    @apply flex-center;
  }
  .img-ratio-center img, .img-ratio-center iframe {
    @apply absolute top-1/2 left-1/2 w-auto h-auto object-contain transition-all duration-300 max-w-full max-h-full -translate-x-1/2 -translate-y-1/2;
  }
  .div-ratio {
    position: relative;
    display: block;
    height: 0;
    overflow: hidden;
    padding-top: 100%;
  }
  .div-ratio > div {
    @apply absolute top-0 left-0 w-full h-full transition-all duration-300;
  }
  body {
    @apply text-[#333333] text-base font-primary;
